.paymentForm {
  display: flex;
  flex-wrap: wrap;
  width: 100%; }
  .paymentForm .input {
    display: flex;
    flex-direction: column;
    padding: 1rem; }
    .paymentForm .input label {
      margin-bottom: 0.5rem; }
    .paymentForm .input input {
      height: 30px;
      line-height: 30px;
      border-radius: 5px;
      font-size: 1.2rem;
      border: 0;
      padding: 2px 5px; }
    .paymentForm .input .error {
      margin-top: 0.5rem;
      color: orangered; }
  .paymentForm .col-6 {
    width: 50%; }
  .paymentForm .col-12 {
    width: 100%; }
  .paymentForm button {
    margin: 2rem auto 0;
    height: 40px;
    line-height: 40px;
    width: 100%;
    max-width: 150px; }
