.menuCard {
  position: relative;
  padding: 1rem;
  cursor: pointer; }
  .menuCard .content {
    height: 100%;
    display: flex;
    justify-content: space-between;
    padding: 1rem; }
  .menuCard .pictureWrapper {
    position: relative;
    min-width: 100px;
    height: 100px;
    overflow: hidden;
    border-radius: 9px; }
    .menuCard .pictureWrapper img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 100%;
      width: auto; }
  .menuCard .texts {
    padding-right: 0.5rem; }
  .menuCard .cardTitle {
    font-size: 1.2rem;
    margin-bottom: 1rem; }
  .menuCard .description {
    color: grey;
    margin-bottom: 1rem; }
  .menuCard .price {
    font-size: 1.1rem;
    color: grey; }
  .menuCard .popular {
    color: orangered;
    margin-left: 0.5rem; }
