* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.layoutLimitedWidth {
  margin: 0 auto;
  width: 100%;
  max-width: 1100px; }

img {
  display: block;
  width: 100%; }

.layout {
  position: relative;
  background-color: #f1f1f1;
  width: 100%;
  min-height: 100vh;
  text-align: center; }

.layoutPageContent {
  padding: 1rem; }

.resetTextAlign {
  text-align: left; }

header {
  text-align: left;
  position: relative;
  height: 60px;
  border-bottom: 1px solid lightgrey;
  background-color: white; }
  header > div {
    height: 100%; }
  header a {
    display: block;
    width: 100%;
    height: 100%; }
  header svg {
    height: 100%;
    width: 12vw; }

.centerContent {
  position: relative;
  text-align: center; }

.layoutContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1100px;
  margin: auto; }

.st0 {
  fill: #00ccbc; }

button,
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 15px;
  height: 25px;
  padding: 2px 5px;
  color: #fff;
  font-size: 1.1rem;
  font-weight: bold;
  line-height: 25px;
  border: 0;
  border-radius: 5px;
  background-color: #00c1b2;
  text-decoration: none; }
  button:hover,
  .btn:hover {
    background-color: #009589; }
  button:disabled,
  .btn:disabled {
    background: lightgrey;
    color: darkgrey;
    cursor: initial; }
